.calendar {
  margin-bottom: 12px;
  padding: 32px;
  font-size: 16px;
  line-height: 1;
  border-radius: 4px;
  background: #fff;
  color: #333;
  box-shadow: 0 0 0 1px #e6e6e6, 3px 3px 0 0 #cacaca; }
  @media screen and (max-width: 47.9375em) {
    .calendar {
      display: none !important; } }
  .calendar__month {
    font-size: 21px;
    text-align: center;
    margin-bottom: 16px;
    position: relative;
    overflow: hidden; }
    .calendar__month-list {
      margin: 0;
      padding: 0;
      list-style: none;
      position: relative;
      height: 33px; }
      .calendar__month-list li {
        position: relative;
        display: block;
        float: left;
        width: 100%;
        margin: 0;
        padding: 0; }
    .calendar__month-next, .calendar__month-prev {
      position: absolute;
      z-index: 999;
      display: block;
      font-weight: 600;
      cursor: pointer;
      top: 8px; }
    .calendar__month-prev {
      left: 8px;
      border-top: 8px solid transparent;
      border-bottom: 8px solid transparent;
      border-right: 8px solid #9A9A9A; }
    .calendar__month-next {
      right: 8px;
      width: 0;
      height: 0;
      border-top: 8px solid transparent;
      border-bottom: 8px solid transparent;
      border-left: 8px solid #9A9A9A; }
  .calendar__weekdays {
