    margin-right: auto;
    display: flex;
    flex-flow: row wrap;
    font-size: 17px;
    color: #1C5D95;
    border-bottom: 1px solid #e6e6e6;
    padding-bottom: 13px;
    margin-bottom: 16px; }
  .calendar__weekday {
    flex: 0 0 14.2857142857%;
    max-width: 14.2857142857%;
    line-height: 32px;
    text-align: center; }
  .calendar__dates {
    max-width: 75rem;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-flow: row wrap; }
  .calendar__date {
    flex: 0 0 14.2857142857%;
    max-width: 14.2857142857%;
    position: relative;
    margin-bottom: 18px;
    line-height: 26px;
    text-align: center;
    cursor: default;
    z-index: 1; }
    .calendar__date:before {
