      left: 2px;
      right: 2px;
      top: 2px;
      bottom: 2px;
      border: 1px solid transparent;
      border-radius: 4px;
      z-index: -1; }
    .calendar__date--current:before {
      border-color: #fbb932;
      background: #fefefe; }
    .calendar__date--has-upcoming-event:before {
      border-color: #fbb932;
      background: #fbb932; }
    .calendar__date--has-previous-event:before {
      border-color: #eef1f5;
      background: #eef1f5; }
    .calendar__date:nth-child(7n - 1), .calendar__date:nth-child(7n) {
      color: #e96100; }
